import { GetComponentProps, styled } from 'topwrite';

export default function Purchase({ props: { value, parameter } }: GetComponentProps<'x-block'>) {
    const purchase = () => {
        window.location.href = `${value}?redirect_to=${encodeURIComponent(location.href)}`;
    };

    const price = Intl.NumberFormat('zh-CN', { style: 'currency', currency: 'CNY' }).format(Number(parameter) / 100);

    return <Container>
        <Title>此章节为付费章节</Title>
        <Desc>现在购买立即解锁全部内容</Desc>
        <Button onClick={purchase}>{price} 立即购买</Button>
    </Container>;
}

const Button = styled.div`
    background-color: var(--ttw-primary-color);
    border-radius: 4px;
    height: 36px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    font-size: 16px;
    cursor: pointer;
    padding: 0 20px;

    &:hover {
        opacity: 0.9;
    }
`;

const Desc = styled.div`
    font-size: 15px;
    margin-bottom: 16px;
    color: var(--ttw-secondary-color);
`;

const Title = styled.div`
    font-size: 18px;
    margin-bottom: 12px;
    color: var(--ttw-color);
`;

const Container = styled.div`
    border: 1px solid var(--ttw-border-color);
    padding: 32px;
    border-radius: 8px;
    background: var(--ttw-box-hover-background);
    text-align: center;
`;
